body 
{
	margin: 0;
	padding: 0;
	background-color: #ffffff;
}

/* CONTAINER */

.container
{
	height: 100%;
	width: 100%;
}

.wrapper
{
	height: 95vh;
	width: 100%;
	margin-top: 50px;
	margin-left: auto;
	margin-right: auto;
	text-align: center;
}

.mobpage{display: none;}
.mainpage{display: block;}

//=================================================================================================================//

/* FONT */

h1
{
	font-size: 8vw;
	margin-bottom: -30px;
}

h2
{
	font-size: 3vw;
	margin-right: 80px;
}

p
{
	font-size: 1.2vw;
	margin-right: auto;
	margin-left: auto;
}

h1,
h2,
h3,
button,
p
{
	text-transform: uppercase;
	font-family: 'Lato', sans-serif;
}

span
{
	color: #2fff05;
}

.paragraph
{
	color: #000000;
}

//=================================================================================================================//

/* NAVIGATION */

#nav-grid
{
	margin-top: 100px;
	display: grid;
	grid-template-columns: repeat(2, 1fr);
	grid-gap: 10px;
	grid-auto-rows: minmax(100px, auto);
	overflow: hidden;
}

nav li
{
	height: 75%;
	list-style: none;
}

nav ul:hover li a
{
	opacity: 0.5;
}

nav ul li a:hover
{
	opacity: 1;
	color: #c00606;
}

.nav-linkz
{
	text-decoration: none;
    color: #000000;
}

.nav-music
{
	width: 40%;
	margin-left: 20%;
	font-family: 'Arial', sans-serif;
	font-size: 50px;
	animation-name: music;
	animation-duration: 2s;
}

@keyframes music
{
	from{ transform: translateX(-500px); opacity: 0; }
	to{transform: translateX(0); opacity: 1;}
}

.nav-artwork
{
	width: 70%;
	margin-left: 20%;
	font-family: 'Arial', sans-serif;
	font-size: 50px;
	animation-name: artwork;
	animation-duration: 2s;
}

@keyframes artwork
{
	from{ transform: translateY(-400px); opacity: 0; }
	to{transform: translateY(0); opacity: 1;}
}

.nav-design
{
	margin-top: 50%;
	margin-left: 20%;
	font-family: 'Arial', sans-serif;
	font-size: 50px;
	animation-name: design;
	animation-duration: 2s;
}

@keyframes design
{
	from{transform: translateY(400px); opacity: 0;}
	to{transform: translateX(0); opacity: 1;}
}

.nav-photo
{
	margin-top: 50%;
	margin-left: 20%;
	font-family: 'Arial', sans-serif;
	font-size: 50px;
	animation-name: photo;
	animation-duration: 2s;
}

@keyframes photo
{
	from{ transform: translateX(500px); opacity: 0; }
	to{transform: translateX(0); opacity: 1;}
}

.red-alert:hover
{
	font-size: 60px;
	animation: shake 0.3s;
	animation-iteration-count: infinite;
}

@keyframes shake 
{
	0% {
	  transform: translate(1px, 1px) rotate(0deg);
	}
	10% {
	  transform: translate(-1px, -2px) rotate(-1deg);
	}
	20% {
	  transform: translate(-3px, 0px) rotate(1deg);
	}
	30% {
	  transform: translate(3px, 2px) rotate(0deg);
	}
	40% {
	  transform: translate(1px, -1px) rotate(1deg);
	}
	50% {
	  transform: translate(-1px, 2px) rotate(-1deg);
	}
	60% {
	  transform: translate(-3px, 1px) rotate(0deg);
	}
	70% {
	  transform: translate(3px, 1px) rotate(-1deg);
	}
	80% {
	  transform: translate(-1px, -1px) rotate(1deg);
	}
	90% {
	  transform: translate(1px, 2px) rotate(0deg);
	}
	100% {
	  transform: translate(1px, -2px) rotate(-1deg);
	}
}

.centreImg
{
	display: block;
	height: 100%;
	width: 100%;
	position: absolute;
	left:0; 
	right:0;	
	top:0; 
	bottom:0;
	z-index: 98;
}

.aboutClick
{
	width: 100%;
	height: 50%;	
	position: absolute;
	bottom: 0;
	text-align: center;
	margin-left: auto;
	margin-right: auto;
	z-index: 99;
}

button
{
	padding: 0;
	outline: none;
	border: none;
	background: none;
	font-size: 2em;
	text-decoration: none; 
	cursor: pointer;
}

//=================================================================================================================//

/* SPOTLIGHT */
.focus
{
	position: absolute;
	display: block;
	width: 100%;
	height: 100%;
	top: 0;
	transform: translateY(100vh);
	background: radial-gradient(circle at var(--x, 50%) var(--y, 50%), rgba(#000000, 10%) 14%, #000000 18%);
	pointer-events: none;
	mix-blend-mode: darken;  
	z-index: -2;
}

/* ================================================================================================ */

/* CLOSE BUTTON */
#close 
{
	position: absolute;
	right: 32px;
	top: 32px;
	width: 32px;
	height: 32px;
	opacity: 0.5;

	&:hover 
	{
		opacity: 1;
	}

	&:before, &:after 
	{
		position: absolute;
		left: 15px;
		content: ' ';
		height: 33px;
		width: 2px;
		background-color: rgb(27, 31, 255);
	}

	&:before
	{
		transform: rotate(45deg);
	}

	&:after 
	{
		transform: rotate(-45deg);
	}
}

.closeAnimation
{
	animation: animateClose 0.6s forwards;
}

//=================================================================================================================//

/* MODAL BOX */

.modal 
{
	display: none;
	position: absolute; 
	left: 0;
	top: 0;
	width: 100%; 
	height: 100%; 
	overflow: hidden;
	background-color: rgba(0,0,0,0.6);
	text-align: center;
	z-index: 99;
}

.modal-content 
{
	position: relative;
	background-color: #fefefe;
	margin: auto;
	height: 100vh;
	padding: 0;
	border: 1px solid #888;
}

.openAnimation{
	animation: animateOpen 0.6s forwards;
}

@keyframes animateOpen 
{
  from {transform:scale(0); opacity:0;}
  to {transform:scale(1); opacity:1;}
}

@keyframes animateClose
{
  from {transform:scale(1); opacity:1}
  to {transform:scale(0); opacity:0}
}

//=================================================================================================================//

/* MOBILE VERSION */

@media screen and (max-width: 800px)
{

	.mobpage{display: block;}
	.mainpage{display: none;}

	.focus
	{
		display: none;
	}

	nav
	{
		a{font-size: x-large;}
		ul:hover li a{opacity: 1;}
		ul li a:hover{color: #000000;}
	}
	
	.row 
	{
		width: 90%;
		text-align: center;
	}

	.grid-container
	 {
		display: inline-flex;
		flex-flow: column wrap;
		padding: 10px;
		list-style: none;
	}

	.centreMobileImg
	{
		display: block;
		height: auto;
		width: 100%;
		max-width: 50%;
		position: relative;
		left:0; 
		right:0;	
		top:0; 
		bottom:0;
		margin-top: 50px;
		margin-right:auto;
		margin-left: auto;
		z-index: 3;
		border-radius: 100%;
	}
}